
.grid {
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(4, 1fr);
    gap: 0.5vw;
    grid-auto-rows: auto;
    grid-template-areas: 
        "a a b b b b b b"
        "a a b b b b b b"
        "c c b b b b b b"
        "c c b b b b b b";
}

.personal {
    grid-area: a;
    align-self: center;
    justify-self: center;
}

.personal img {
    width: auto;
    height: 30vh;
    margin: auto;
    padding-top: 2vh;
}

.message {
    grid-area: b;
    padding-top: 2vh;
}

.decoration {
    grid-area: c;
    align-self: top;
    justify-self: center;
}

.underline {
    text-decoration: underline;
}

.item1 {
    padding-top: 2vh;
}

.item2  {
    padding-top: 4vh;
}

.item3 {
    padding-top: 8vh;
}

.item4  {
    padding-top: 10vh;
}

.item5 {
    padding-top: 12vh;
}

.item6  {
    padding-top: 14vh;
}

.item7 {
    padding-top: 16vh;
}

.item8  {
    padding-top: 20vh;
}

